<template>
  <div class="v-select">
    <p class="title" @click="areOptionsVisible = !areOptionsVisible">
      {{ selected }}
    </p>
    <div class="options" v-if="areOptionsVisible">
      <p
        v-for="option in options"
        :key="option.value"
        @click="selectOption(option)"
      >
        {{ option.name }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "v-select",
  props: {
    options: {
      type: Array,
      default() {
        return [];
      },
    },
    selected: {
      type: String,
      default() {
        return "";
      },
    },
  },
  data() {
    return {
      areOptionsVisible: false,
    };
  },
  methods: {
    selectOption(option) {
      this.$emit("select", option);

      this.areOptionsVisible = false;
    },
    hideSelect() {
      this.areOptionsVisible = false;
    },
  },
  mounted() {
    document.addEventListener("click", this.hideSelect.bind(this), true);
  },
  beforeDestroy() {
    document.removeEventListener("click", this.hideSelect);
  },
};
</script>

<style>
.v-select {
  position: absolute;
  top: 5px;
  left: 10px;
  padding: 20px;
  border: solid 1px;
  width: 150px;
  cursor: pointer;
}
.title {
  border: solid 1px #aeaeae;
  padding: 8px;
}
.v-select p {
  margin: 0;
}

.options {
  border: solid 1px #aeaeae;
  background: #ffffff;
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  padding: 8px;
  font-size: 40px;
}

.options p:hover {
  background: #e7e7e7;
}
@media screen and (min-width: 100px) and (max-width: 767px) {
  .v-select {
    position: absolute;
    top: 5px;
    left: 10px;
    padding: 20px;
    border: solid 1px;
    width: 50px;
    cursor: pointer;
  }
  .options {
    border: solid 1px #aeaeae;
    background: #ffffff;
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    padding: 8px;
    font-size: 14px;
  }
}
</style>
